


import React from 'react'
import { useDispatch } from 'react-redux'
import { changeContact } from '../../Redux/contactsSliceOperations'

const Modal = ({ toggleModal, getContacts, id, newName, setNewName, newNumber, setNewNumber }) => {
    
    const dispath = useDispatch()

    const changeOldContact = () => {

        dispath(changeContact({newName, newNumber, id}))
        dispath(getContacts())
        toggleModal()
    }

  return (
      <div className='modal'>
        <h2> </h2>
        <input placeholder='' value={newName} onChange={(e) => setNewName(e.target.value)}></input>
        <input placeholder='' value={newNumber} onChange={(e) => setNewNumber(e.target.value)}></input>
        <button onClick={() => changeOldContact()}></button>
        <button onClick={() => toggleModal()}></button>
      </div>
  )
}

export default Modal